import React, { useState, useEffect, useRef } from "react";

import axios from "axios";

import { FaInstagram } from "react-icons/fa";
import { FaFacebook } from "react-icons/fa";
import { FaLinkedin } from "react-icons/fa";

import Navbar from "../../genral_components/Navbar/Navbar";

import Footer from "../../genral_components/Footer/Footer";

import "./BlogNewPage.css";

export default function BlogNewPage() {
  const postId = useRef("Initial Id");

  const [posts, setPosts] = useState();

  useEffect(() => {
    // Retrieve the hash value and parse the data to set the props accordingly
    const hashData = window.location.hash.substr(1);

    console.log("Hash data", hashData);

    const data = JSON.parse(decodeURIComponent(hashData));

    console.log("Data", data);

    postId.current = data.id;

    let url = `https://dev-interac8digital.pantheonsite.io/wp-json/wp/v2/posts/${postId.current}?_embed`;

    axios.get(url).then((response) => {
      setPosts(response.data);
    });

    // Do something with the props...
  }, []);

  return (
    <div className="blog-new-page-main-container">
      <Navbar />

      <div className="blog-new-page-main-container-secondary">
        <div className="blog-new-page-main-container-secondary-main-image">
          {posts && (
            <img
              src={
                posts._embedded["wp:featuredmedia"][0].media_details.sizes.full
                  .source_url
              }
              className="banner-image"
              alt="post banner"
            />
          )}
        </div>

        {posts && (
          <div
            className="blog-new-page-main-container-secondary-content"
            dangerouslySetInnerHTML={{ __html: posts.content.rendered }}
          ></div>
        )}

        <div className="share-this-post">
          <p>Share this post</p>

          <div className="share-this-post-social-media">
            <FaInstagram size={25} color="#612D91" />
            <FaFacebook size={25} color="#612D91" />
            <FaLinkedin size={25} color="#612D91" />
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
}
